<template>
  <div class="headerWrapper">
    <div class="header">
      <div class="container">
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'InHeader',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .headerWrapper {
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 1500;
    height: 80px;
  }
  .header {
    height: 80px;
    background-color: #fff;
    color: #fff;
    top: 0;
    left: 0;
    width: 100%;
    line-height: 80px;
    z-index: 100;
    position: relative;
  }
  .container {
    padding: 0;
    height: 100%;
    box-sizing: border-box;
    background: #ddd;
  }
</style>



